export interface Day {
    id: number;
    pic?: string;
    name: string;
    active: boolean;
    done: boolean;
    penalty: boolean;
    type: DayType;
    icon?: string;
    time: string;
    games: Game[];
}

export interface Game {
    id: number;
    title: string;
    pic: string;
    picHeader: string;
    available: boolean;
    done: boolean;
    type: GameType;
    test?: TestQuestion[];
    complete: {
        pic?: string;
        title: string;
        lose?: string;
        text: string;
    };
    description: string;
}

export interface TestQuestion {
    id: number;
    question: string;
    answers: {
        id: number;
        text: string;
    }[];
}

export interface ApiGame {
    game_title: string;
    day_number: string;
    game_completed: boolean;
    complete_date: string;
}

export interface ApiDay {
    day_number: string;
    day_title: string;
    day_status: string;
    day_open_date: string;
    day_complete_time: string;
    day_games: ApiGame[];
}

