import { Button } from "../../components/Button/Button";
import { Title } from "../../components/Title/Title";
import { Game } from "../../store/main.reducer";
import "swiper/css";
import { Swiper, SwiperSlide } from "swiper/react";
import css from "./ChoosePicGame.module.css";
import { useState } from "react";
import useIsMobile from "../../hooks/useIsMobile";
import classNames from "classnames";

export interface ChoosePicGameProps {
    game?: Game;
    dayID?: number;
    gameID?: number;
    checkGame: (dayID: number, gameID: number) => {};
}

export default function ChoosePicGame({
    game,
    dayID,
    gameID,
    checkGame,
}: ChoosePicGameProps) {
    interface PicGame {
        title: string;
        pics: {
            id: number;
            pic: string;
            title: string;
        }[];
    }

    interface PicGames {
        [key: number]: PicGame;
    }

    const picGames: PicGames = {
        8: {
            title: "Выберите картинку, которая лучше всего отражает, какое отложенное дело вы могли бы начать уже сегодня.",
            pics: [
                {
                    id: 1,
                    pic: "assets/images/days/day2/game3/pic1.png",
                    title: "Поход в театр",
                },
                {
                    id: 2,
                    pic: "assets/images/days/day2/game3/pic2.png",
                    title: "Поход в театр",
                },
                {
                    id: 3,
                    pic: "assets/images/days/day2/game3/pic2.png",
                    title: "Поход в театр",
                },
            ],
        },
        13: {
            title: "Выберите картинку, которая больше всего отражает ваше сегодняшнее отложенное дело",
            pics: [
                {
                    id: 1,
                    pic: "assets/images/days/day3/game3/pic1.png",
                    title: "Горы бумаг",
                },
                {
                    id: 2,
                    pic: "assets/images/days/day3/game3/pic2.png",
                    title: "Горы бумаг",
                },
                {
                    id: 3,
                    pic: "assets/images/days/day3/game3/pic2.png",
                    title: "Горы бумаг",
                },
            ],
        },
        24: {
            title: "Выберите картинку, которая больше всего отражает ваш следующий шаг после этого проекта.",
            pics: [
                {
                    id: 1,
                    pic: "assets/images/days/day5/game4/pic1.png",
                    title: "Горы бумаг",
                },
                {
                    id: 2,
                    pic: "assets/images/days/day5/game4/pic2.png",
                    title: "Горы бумаг",
                },
                {
                    id: 3,
                    pic: "assets/images/days/day5/game4/pic2.png",
                    title: "Горы бумаг",
                },
            ],
        },
    };

    const getCurrentPicGame = (gameId: number): PicGame | null => {
        return picGames[gameId] || null;
    };

    const currentPicGame = getCurrentPicGame(gameID ? gameID : 0);
    const [activeIndex, setActiveIndex] = useState<number | null>(null);
    const isMobile = useIsMobile();

    return (
        <div className={css["choose-pic-game"]}>
            <Title className={css["choose-pic-game__title"]}>
                {currentPicGame?.title}
            </Title>
            <div className={css["choose-pic-swiper-wrapper"]}>
                <Swiper
                    className={css["choose-pic-swiper"]}
                    onSlideChange={(swiper: any) =>
                        setActiveIndex(swiper.activeIndex)
                    }
                    onSwiper={(swiper: any) =>
                        isMobile && setActiveIndex(swiper.activeIndex)
                    }
                    breakpoints={{
                        320: {
                            slidesPerView: "auto",
                            centeredSlides: true,
                            spaceBetween: 20,
                        },
                        1200: {
                            slidesPerView: 3,
                            spaceBetween: 30,
                        },
                    }}
                >
                    {currentPicGame?.pics.map((pic, index) => {
                        return (
                            <SwiperSlide>
                                <div
                                    className={classNames(
                                        css["choose-pic-slide"],
                                        activeIndex === index &&
                                            css["choose-pic-slide_active"],
                                        activeIndex === null &&
                                            css["choose-pic-slide_default"]
                                    )}
                                    onClick={() =>
                                        !isMobile && setActiveIndex(index)
                                    }
                                >
                                    <div
                                        className={
                                            css["choose-pic-slide__content"]
                                        }
                                    >
                                        <div
                                            className={
                                                css["choose-pic-slide__image"]
                                            }
                                        >
                                            <img
                                                src={pic.pic}
                                                width={300}
                                                alt=""
                                            />
                                        </div>
                                    </div>
                                </div>
                                <Title
                                    className={classNames(
                                        css["choose-pic-slide__title"],
                                        activeIndex === index &&
                                            css[
                                                "choose-pic-slide__title_active"
                                            ]
                                    )}
                                    size="small"
                                >
                                    {pic.title}
                                </Title>
                            </SwiperSlide>
                        );
                    })}
                </Swiper>
            </div>
            <Button
                color="golden"
                className={css["choose-pic-game__submit-button"]}
                onClick={() =>
                    checkGame(dayID ? dayID : 0, gameID ? gameID : 0)
                }
                disabled={activeIndex === null}
            >
                Завершить
            </Button>
        </div>
    );
}
