import { useState } from "react";
import "swiper/css";
import { Navigation } from "swiper/modules";
import { Swiper, SwiperSlide } from "swiper/react";
import { Button } from "../../components/Button/Button";
import { Title } from "../../components/Title/Title";
import useIsMobile from "../../hooks/useIsMobile";
import { Game } from "../../store/main.reducer";
import css from "./CardMatchGame.module.css";

export interface CardMatchGameProps {
    game?: Game;
    dayID?: number;
    gameID?: number;
    checkGame: (dayID: number, gameID: number) => {};
}

export default function CardMatchGame({
    game,
    dayID,
    gameID,
    checkGame,
}: CardMatchGameProps) {
    const isMobile = useIsMobile();

    interface Card {
        id: number;
        pic: string;
        title: string;
    }

    interface DraggedCard {
        id: number;
        pic: string;
        title: string;
        padId: number;
        originalIndex: number;
    }

    const pads: Card[] = [
        {
            id: 1,
            pic: "assets/images/days/day2/game1/card1.png",
            title: "Заявка на фриланс",
        },
        {
            id: 2,
            pic: "assets/images/days/day2/game1/card2.png",
            title: "Изучение китайского",
        },
        {
            id: 3,
            pic: "assets/images/days/day2/game1/card3.png",
            title: "Запись на скалодром",
        },
        {
            id: 4,
            pic: "assets/images/days/day2/game1/card4.png",
            title: "-1 иконка на рабочем столе",
        },
    ];

    const initialItems: Card[] = [
        {
            id: 1,
            pic: "assets/images/days/day2/game1/card11.png",
            title: "Стало по-настоящему волнительно",
        },
        {
            id: 2,
            pic: "assets/images/days/day2/game1/card22.png",
            title: "На одном языке с маркетплейсами",
        },
        {
            id: 3,
            pic: "assets/images/days/day2/game1/card33.png",
            title: "Чувствую новые горизонты",
        },
        {
            id: 4,
            pic: "assets/images/days/day2/game1/card44.png",
            title: "Мир стал чище",
        },
    ];

    const [items, setItems] = useState<Card[]>(initialItems);
    const [draggedCards, setDraggedCards] = useState<DraggedCard[]>([]);
    const [activeCard, setActiveCard] = useState<number | null>(null);
    const [usedItems, setUsedItems] = useState<number[]>([]);
    const [activePadIndex, setActivePadIndex] = useState(0);

    const handleDragStart = (e: React.DragEvent, item: Card, index: number) => {
        e.dataTransfer.setData("cardId", item.id.toString());
        e.dataTransfer.setData("originalIndex", index.toString());
    };

    const handleDragOver = (e: React.DragEvent, padId: number) => {
        e.preventDefault();
    };

    const handleDrop = (e: React.DragEvent, padId: number) => {
        e.preventDefault();
        const cardId = parseInt(e.dataTransfer.getData("cardId"));
        const originalIndex = parseInt(e.dataTransfer.getData("originalIndex"));

        const item = items.find((item) => item.id === cardId);
        if (!item) return;

        // Проверяем, не занят ли уже этот pad
        if (draggedCards.some((card) => card.padId === padId)) return;

        // Добавляем карточку в draggedCards
        const newDraggedCard: DraggedCard = {
            ...item,
            padId,
            originalIndex,
        };

        setDraggedCards((prev) => [...prev, newDraggedCard]);

        // Удаляем карточку из нижнего ряда
        // setItems((prev) => prev.filter((item) => item.id !== cardId));
        setUsedItems((prev) => [...prev, cardId]);
    };

    const handleCancel = (card: DraggedCard) => {
        // Возвращаем карточку в исходный ряд
        setItems((prev) => {
            const newItems = [...prev];
            newItems.splice(card.originalIndex, 0, {
                id: card.id,
                pic: card.pic,
                title: card.title,
            });
            return newItems;
        });

        // Удаляем карточку из draggedCards
        setDraggedCards((prev) => prev.filter((c) => c.id !== card.id));
        setUsedItems((prev) => prev.filter((id) => id !== card.id));
    };

    const handleCardMouseEnter = (cardId: number) => {
        setActiveCard(cardId);
    };

    const handleCardMouseLeave = () => {
        setActiveCard(null);
    };

    const handleMobileItemClick = (item: Card) => {
        const activePad = pads[activePadIndex];
        if (!activePad) return;

        // Проверяем, не занят ли уже этот pad
        if (draggedCards.some((card) => card.padId === activePad.id)) return;

        // Добавляем карточку в draggedCards
        const newDraggedCard: DraggedCard = {
            ...item,
            padId: activePad.id,
            originalIndex: initialItems.findIndex((i) => i.id === item.id),
        };

        setDraggedCards((prev) => [...prev, newDraggedCard]);
        setUsedItems((prev) => [...prev, item.id]);
    };

    return (
        <div className={css["card-match-game"]}>
            {isMobile ? (
                <>
                    <div className={css["card-pads-swiper"]}>
                        <Swiper
                            slidesPerView={"auto"}
                            centeredSlides={true}
                            spaceBetween={30}
                            className={css["pads-swiper"]}
                            allowTouchMove={false}
                            modules={[Navigation]}
                            navigation={true}
                            onSlideChange={(swiper: any) =>
                                setActivePadIndex(swiper.activeIndex)
                            }
                        >
                            {pads.map((pad) => {
                                const draggedCard = draggedCards.find(
                                    (card) => card.padId === pad.id
                                );

                                return (
                                    <SwiperSlide>
                                        <div
                                            key={pad.id}
                                            className={css["card-pad"]}
                                            onDragOver={(e) =>
                                                handleDragOver(e, pad.id)
                                            }
                                            onDrop={(e) =>
                                                handleDrop(e, pad.id)
                                            }
                                        >
                                            <div
                                                className={
                                                    css["card-pad__content"]
                                                }
                                            >
                                                <div
                                                    className={
                                                        css["card-pad__image"]
                                                    }
                                                >
                                                    <img
                                                        src={pad.pic}
                                                        alt={pad.title}
                                                    />
                                                </div>
                                                <div
                                                    className={
                                                        css["card-pad__title"]
                                                    }
                                                >
                                                    {pad.title}
                                                </div>
                                            </div>

                                            {draggedCard && (
                                                <div
                                                    className={
                                                        css[
                                                            "card-item--dragged"
                                                        ]
                                                    }
                                                    onMouseEnter={() =>
                                                        handleCardMouseEnter(
                                                            draggedCard.id
                                                        )
                                                    }
                                                    onMouseLeave={
                                                        handleCardMouseLeave
                                                    }
                                                >
                                                    <div
                                                        className={
                                                            css[
                                                                "card-item__content"
                                                            ]
                                                        }
                                                    >
                                                        <div
                                                            className={
                                                                css[
                                                                    "card-item__image"
                                                                ]
                                                            }
                                                        >
                                                            <img
                                                                src={
                                                                    draggedCard.pic
                                                                }
                                                                alt={
                                                                    draggedCard.title
                                                                }
                                                            />
                                                        </div>
                                                        <div
                                                            className={
                                                                css[
                                                                    "card-item__title"
                                                                ]
                                                            }
                                                        >
                                                            {draggedCard.title}
                                                        </div>
                                                    </div>
                                                    <button
                                                        className={
                                                            css[
                                                                "card-item__cancel"
                                                            ]
                                                        }
                                                        onClick={() =>
                                                            handleCancel(
                                                                draggedCard
                                                            )
                                                        }
                                                    ></button>
                                                </div>
                                            )}
                                        </div>
                                    </SwiperSlide>
                                );
                            })}
                        </Swiper>
                    </div>
                    <div className={css["card-items-swiper"]}>
                        <Swiper
                            slidesPerView={"auto"}
                            centeredSlides={true}
                            spaceBetween={20}
                            className={css["items-swiper"]}
                            allowTouchMove={false}
                            modules={[Navigation]}
                            navigation={true}
                        >
                            {initialItems.map((item, index) => (
                                <SwiperSlide>
                                    {usedItems.includes(item.id) ? (
                                        <div
                                            key={item.id}
                                            className={css["card-item_used"]}
                                        />
                                    ) : (
                                        <div
                                            key={item.id}
                                            className={css["card-item"]}
                                            draggable
                                            onDragStart={(e) =>
                                                !isMobile &&
                                                handleDragStart(e, item, index)
                                            }
                                            onClick={() =>
                                                isMobile &&
                                                handleMobileItemClick(item)
                                            }
                                        >
                                            <div
                                                className={
                                                    css["card-item__content"]
                                                }
                                            >
                                                <div
                                                    className={
                                                        css["card-item__image"]
                                                    }
                                                >
                                                    <img
                                                        src={item.pic}
                                                        alt={item.title}
                                                    />
                                                </div>
                                                <div
                                                    className={
                                                        css["card-item__title"]
                                                    }
                                                >
                                                    {item.title}
                                                </div>
                                            </div>
                                        </div>
                                    )}
                                </SwiperSlide>
                            ))}
                        </Swiper>
                    </div>
                </>
            ) : (
                <>
                    <div className={css["card-pads"]}>
                        {pads.map((pad) => {
                            const draggedCard = draggedCards.find(
                                (card) => card.padId === pad.id
                            );
                            return (
                                <div
                                    key={pad.id}
                                    className={css["card-pad"]}
                                    onDragOver={(e) =>
                                        handleDragOver(e, pad.id)
                                    }
                                    onDrop={(e) => handleDrop(e, pad.id)}
                                >
                                    <div className={css["card-pad__content"]}>
                                        <div className={css["card-pad__image"]}>
                                            <img
                                                src={pad.pic}
                                                alt={pad.title}
                                            />
                                        </div>
                                        <div className={css["card-pad__title"]}>
                                            {pad.title}
                                        </div>
                                    </div>

                                    {draggedCard && (
                                        <div
                                            className={
                                                css["card-item--dragged"]
                                            }
                                            onMouseEnter={() =>
                                                handleCardMouseEnter(
                                                    draggedCard.id
                                                )
                                            }
                                            onMouseLeave={handleCardMouseLeave}
                                        >
                                            <div
                                                className={
                                                    css["card-item__content"]
                                                }
                                            >
                                                <div
                                                    className={
                                                        css["card-item__image"]
                                                    }
                                                >
                                                    <img
                                                        src={draggedCard.pic}
                                                        alt={draggedCard.title}
                                                    />
                                                </div>
                                                <div
                                                    className={
                                                        css["card-item__title"]
                                                    }
                                                >
                                                    {draggedCard.title}
                                                </div>
                                            </div>

                                            {activeCard === draggedCard.id && (
                                                <button
                                                    className={
                                                        css["card-item__cancel"]
                                                    }
                                                    onClick={() =>
                                                        handleCancel(
                                                            draggedCard
                                                        )
                                                    }
                                                ></button>
                                            )}
                                        </div>
                                    )}
                                </div>
                            );
                        })}
                    </div>
                    <div className={css["card-items"]}>
                        {initialItems.map((item, index) => {
                            if (usedItems.includes(item.id)) {
                                return (
                                    <div
                                        key={item.id}
                                        className={css["card-item_used"]}
                                    />
                                );
                            }
                            return (
                                <div
                                    key={item.id}
                                    className={css["card-item"]}
                                    draggable
                                    onDragStart={(e) =>
                                        handleDragStart(e, item, index)
                                    }
                                >
                                    <div className={css["card-item__content"]}>
                                        <div
                                            className={css["card-item__image"]}
                                        >
                                            <img
                                                src={item.pic}
                                                alt={item.title}
                                            />
                                        </div>
                                        <div
                                            className={css["card-item__title"]}
                                        >
                                            {item.title}
                                        </div>
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                </>
            )}
            <div className={css["card-match-game__hint"]}>
                <Title
                    size="small"
                    className={css["card-match-game__hint-title"]}
                >
                    Перенесите карточки к нужным парам
                </Title>
            </div>
            <Button
                color="golden"
                disabled={usedItems.length === 4 ? false : true}
                className={css["card-match-game__submit-button"]}
                onClick={() =>
                    checkGame(dayID ? dayID : 0, gameID ? gameID : 0)
                }
            >
                Далее
            </Button>
        </div>
    );
}
