import { useState } from "react";
import { Button } from "../../components/Button/Button";
import { Title } from "../../components/Title/Title";
import { Game } from "../../store/main.reducer";
import css from "./PicCommentGame.module.css";

export interface PicCommentGameProps {
    game?: Game;
    dayID?: number;
    gameID?: number;
    checkGame: (dayID: number, gameID: number) => {};
}

export default function PicCommentGame({
    game,
    dayID,
    gameID,
    checkGame,
}: PicCommentGameProps) {
    interface CommentGame {
        title: string;
        pic: string;
    }

    interface CommentGames {
        [key: number]: CommentGame;
    }

    const picGames: CommentGames = {
        10: {
            title: "Самое время придумать забавную подпись к этому фото:",
            pic: "assets/images/days/day2/game5/game-pic.png",
        },
    };

    const getCurrentCommentGame = (gameId: number): CommentGame | null => {
        return picGames[gameId] || null;
    };

    const currentPicGame = getCurrentCommentGame(gameID ? gameID : 0);

    const [text, setText] = useState("");

    return (
        <div className={css["pic-comment-game"]}>
            <Title className={css["pic-comment-game__title"]}>
                Самое время придумать забавную подпись к этому фото:
            </Title>
            <div className={css["pic-comment-block"]}>
                <div className={css["pic-comment-image"]}>
                    <div className={css["pic-comment-image__content"]}>
                        <img src={currentPicGame?.pic} alt="" />
                    </div>
                </div>
                <div className={css["pic-comment-textarea"]}>
                    <div className={css["pic-comment-textarea__content"]}>
                        <textarea
                            placeholder="Начните вводить..."
                            maxLength={400}
                            onChange={(e) => setText(e.target.value)}
                        ></textarea>
                    </div>
                </div>
            </div>
            <Button
                onClick={() =>
                    checkGame(dayID ? dayID : 0, gameID ? gameID : 0)
                }
                disabled={text.length === 0}
                color="golden"
                className={css["pic-comment-game__submit"]}
            >
                Отправить
            </Button>
        </div>
    );
}
