import classNames from "classnames";
import { useState } from "react";
import { Button } from "../../components/Button/Button";
import { Text } from "../../components/Text/Text";
import { Title } from "../../components/Title/Title";
import { useAppDispatch } from "../../store";
import { completeGame, Game } from "../../store/main.reducer";
import css from "./PhrazleGame.module.css";

export interface PhrazleGameProps {
    game?: Game;
    dayID?: number;
    gameID?: number;
    correctWord: string;
    availableLetters: string[];
    checkGame: (dayID: number, gameID: number) => {};
}

export default function PhrazleGame({
    game,
    dayID,
    gameID,
    correctWord,
    availableLetters,
    checkGame,
}: PhrazleGameProps) {
    const [userInput, setUserInput] = useState<string[]>(
        Array(correctWord.length).fill("")
    );
    const dispatch = useAppDispatch();
    const [currentPosition, setCurrentPosition] = useState<number>(0);
    const [gameCompleted, setGameCompleted] = useState<boolean>(false);
    const [isCorrect, setIsCorrect] = useState<boolean | null>(null);
    const [usedLetters, setUsedLetters] = useState<boolean[]>(
        Array(availableLetters.length).fill(false)
    );

    // Обработчик клика по букве
    const handleLetterClick = (index: number) => {
        if (
            currentPosition < correctWord.length &&
            !gameCompleted &&
            !usedLetters[index]
        ) {
            const newUserInput = [...userInput];
            newUserInput[currentPosition] = availableLetters[index];
            setUserInput(newUserInput);
            setCurrentPosition(currentPosition + 1);

            // Помечаем букву как использованную
            const newUsedLetters = [...usedLetters];
            newUsedLetters[index] = true;
            setUsedLetters(newUsedLetters);
        }
    };

    // Обработчик клика по букве в целевой области (для удаления)
    const handleTargetLetterClick = (index: number) => {
        if (!gameCompleted && userInput[index] !== "") {
            // Находим все индексы использованных букв в availableLetters
            const usedIndices: number[] = [];
            availableLetters.forEach((letter, i) => {
                if (usedLetters[i]) {
                    usedIndices.push(i);
                }
            });

            // Находим индекс последней использованной буквы
            const lastUsedIndex = usedIndices[usedIndices.length - 1];

            if (lastUsedIndex !== undefined) {
                // Возвращаем только последнюю букву в доступные
                const newUsedLetters = [...usedLetters];
                newUsedLetters[lastUsedIndex] = false;
                setUsedLetters(newUsedLetters);

                // Удаляем только последнюю букву из целевого слова
                const newUserInput = [...userInput];
                newUserInput[currentPosition - 1] = "";
                setUserInput(newUserInput);
                setCurrentPosition(currentPosition - 1);
            }
        }
    };

    // Альтернативный вариант: удаление с любой позиции с корректным возвратом букв
    const handleTargetLetterClickAlternative = (index: number) => {
        if (!gameCompleted && userInput[index] !== "") {
            // Создаем копии массивов
            const newUserInput = [...userInput];
            const newUsedLetters = [...usedLetters];

            // Находим все использованные позиции
            const usedPositions: number[] = [];
            newUserInput.forEach((letter, i) => {
                if (letter !== "") {
                    usedPositions.push(i);
                }
            });

            // Если кликнули на последнюю букву - просто удаляем её
            if (index === usedPositions[usedPositions.length - 1]) {
                // Находим индекс этой буквы в availableLetters
                const letterIndex = availableLetters.findIndex(
                    (letter, i) =>
                        letter === newUserInput[index] && newUsedLetters[i]
                );

                if (letterIndex !== -1) {
                    newUsedLetters[letterIndex] = false;
                    newUserInput[index] = "";
                    setUsedLetters(newUsedLetters);
                    setUserInput(newUserInput);
                    setCurrentPosition(index);
                }
            } else {
                // Если кликнули не на последнюю букву - сбрасываем все начиная с этой позиции
                for (let i = index; i < correctWord.length; i++) {
                    if (newUserInput[i] !== "") {
                        // Находим и возвращаем каждую букву
                        const letterIndex = availableLetters.findIndex(
                            (letter, idx) =>
                                letter === newUserInput[i] &&
                                newUsedLetters[idx]
                        );
                        if (letterIndex !== -1) {
                            newUsedLetters[letterIndex] = false;
                        }
                        newUserInput[i] = "";
                    }
                }
                setUsedLetters(newUsedLetters);
                setUserInput(newUserInput);
                setCurrentPosition(index);
            }
        }
    };

    // Улучшенная версия: удаление с любой позиции
    const handleTargetLetterClickImproved = (index: number) => {
        if (!gameCompleted && userInput[index] !== "") {
            const newUserInput = [...userInput];
            const newUsedLetters = [...usedLetters];

            // Определяем, с какой позиции начинать удаление
            // Если это не последняя буква, удаляем все начиная с этой позиции
            const isLastLetter = index === currentPosition - 1;

            if (isLastLetter) {
                // Удаляем только одну букву (последнюю)
                const letterIndex = availableLetters.findIndex(
                    (letter, i) =>
                        letter === newUserInput[index] && newUsedLetters[i]
                );

                if (letterIndex !== -1) {
                    newUsedLetters[letterIndex] = false;
                    newUserInput[index] = "";
                    setUsedLetters(newUsedLetters);
                    setUserInput(newUserInput);
                    setCurrentPosition(index);
                }
            } else {
                // Удаляем все буквы начиная с выбранной позиции
                for (let i = index; i < correctWord.length; i++) {
                    if (newUserInput[i] !== "") {
                        const letterIndex = availableLetters.findIndex(
                            (letter, idx) =>
                                letter === newUserInput[i] &&
                                newUsedLetters[idx]
                        );
                        if (letterIndex !== -1) {
                            newUsedLetters[letterIndex] = false;
                        }
                        newUserInput[i] = "";
                    }
                }
                setUsedLetters(newUsedLetters);
                setUserInput(newUserInput);
                setCurrentPosition(index);
            }
        }
    };

    // Проверка результата
    const checkResult = () => {
        const userWord = userInput.join("");
        const correct = userWord === correctWord;
        setIsCorrect(correct);
        setGameCompleted(true);
        if (correct) {
            dispatch(completeGame({ dayID: dayID, gameID: gameID }));
            checkGame(dayID ? dayID : 0, gameID ? gameID : 0);
        }
    };

    // Сброс игры
    const resetGame = () => {
        setUserInput(Array(correctWord.length).fill(""));
        setCurrentPosition(0);
        setGameCompleted(false);
        setIsCorrect(null);
        setUsedLetters(Array(availableLetters.length).fill(false));
    };

    return (
        <div className={css["phrazle-game"]}>
            <Title className={css["phrazle-game__title"]}>
                Фраза из букв — составьте слово
            </Title>

            {/* Целевое слово */}
            <div className={css["target-word"]}>
                <div className={css["target-word__content"]}>
                    <div className={css["word-value"]}>
                        {userInput.map((letter, index) => (
                            <div
                                key={index}
                                className={classNames(
                                    css["word-value__letter"],
                                    index < currentPosition &&
                                        css["word-value__letter_active"],
                                    gameCompleted &&
                                        isCorrect &&
                                        css["word-value__letter_correct"],
                                    gameCompleted &&
                                        !isCorrect &&
                                        css["word-value__letter_incorrect"]
                                )}
                                onClick={() =>
                                    handleTargetLetterClickImproved(index)
                                }
                            >
                                {letter || ""}
                            </div>
                        ))}
                    </div>
                </div>
            </div>

            {/* Доступные буквы */}
            <div className={css["word-letters"]}>
                {availableLetters.map((letter, index) => (
                    <div
                        key={index}
                        className={classNames(
                            css["word-letters__letter"],
                            usedLetters[index] &&
                                css["word-letters__letter_active"],
                            !usedLetters[index] &&
                                css["word-letters__letter_available"]
                        )}
                        onClick={() =>
                            !usedLetters[index] && handleLetterClick(index)
                        }
                    >
                        {!usedLetters[index] ? letter : ""}
                    </div>
                ))}
            </div>

            {/* Подсказка */}
            <div
                className={classNames(
                    css["target-word__hint"],
                    !isCorrect &&
                        gameCompleted &&
                        css["target-word__hint_error"]
                )}
            >
                {!isCorrect && gameCompleted ? (
                    <Text color="white">
                        ЭТО НЕ ТО СЛОВО, КОТОРОЕ МЫ ЗАГАДАЛИ
                    </Text>
                ) : (
                    <Text>Нажимайте буквы в правильном порядке</Text>
                )}
            </div>

            {/* Кнопки управления */}
            <div className={css["game-controls"]}>
                {!gameCompleted ? (
                    <Button
                        color="golden"
                        className={css["target-word__submit"]}
                        onClick={checkResult}
                        disabled={currentPosition !== correctWord.length}
                    >
                        Проверить
                    </Button>
                ) : (
                    <>
                        <Button
                            color="golden"
                            className={css["target-word__submit"]}
                            onClick={resetGame}
                        >
                            СБРОСИТЬ
                        </Button>
                    </>
                )}
            </div>
        </div>
    );
}
