import { useState } from "react";
import { Button } from "../../components/Button/Button";
import { Title } from "../../components/Title/Title";
import { Game } from "../../store/main.reducer";
import css from "./NumberInputGame.module.css";

export interface NumberInputGameProps {
    game?: Game;
    dayID?: number;
    gameID?: number;
    checkGame: (dayID: number, gameID: number) => {};
}

export default function NumberInputGame({
    game,
    dayID,
    gameID,
    checkGame,
}: NumberInputGameProps) {
    interface FeedbackTexts {
        [key: number]: string;
    }

    const [inputValue, setInputValue] = useState<string>("");
    const [showFeedback, setShowFeedback] = useState<boolean>(false);

    const feedbackTexts: FeedbackTexts = {
        1: "Каждое «когда-нибудь» заслуживает начала. «Сегодня» — лучший выбор",
        2: "Каждое «когда-нибудь» заслуживает начала. «Сегодня» — лучший выбор 2",
        3: "Каждое «когда-нибудь» заслуживает начала. «Сегодня» — лучший выбор 3",
        4: "Каждое «когда-нибудь» заслуживает начала. «Сегодня» — лучший выбор 4",
        5: "Каждое «когда-нибудь» заслуживает начала. «Сегодня» — лучший выбор 5",
        6: "Каждое «когда-нибудь» заслуживает начала. «Сегодня» — лучший выбор 6",
        7: "Каждое «когда-нибудь» заслуживает начала. «Сегодня» — лучший выбор 7",
        8: "Каждое «когда-нибудь» заслуживает начала. «Сегодня» — лучший выбор 8",
        9: "Каждое «когда-нибудь» заслуживает начала. «Сегодня» — лучший выбор 9",
    };

    const handleNumberClick = (number: number) => {
        if (inputValue.length < 1) {
            // Максимум 1 цифра
            setInputValue(inputValue + number);
            setShowFeedback(false);
        }
    };

    const handleDelete = () => {
        setInputValue("");
        setShowFeedback(false);
    };

    const handleSubmit = () => {
        if (inputValue && inputValue !== "0") {
            setShowFeedback(true);
        }
    };

    const getFeedbackText = (): string => {
        const num = parseInt(inputValue);
        return feedbackTexts[num] || "Пожалуйста, введите число от 1 до 9";
    };

    return (
        <div className={css["number-input-game"]}>
            <Title className={css["number-input-game__title"]}>
                Введите число — сколько дел вы сейчас
                <br />
                откладываете. Получите фидбек
            </Title>
            <div className={css["input-block"]}>
                <div className={css["input-block__content"]}>
                    <div className={css["input-block__screen"]}>
                        {showFeedback ? (
                            <span className={css["input-block__screen-text"]}>
                                {getFeedbackText()}
                            </span>
                        ) : (
                            <span className={css["input-block__screen-number"]}>
                                {inputValue || "-"}
                            </span>
                        )}
                    </div>

                    <div className={css["input-block__numbers"]}>
                        <button onClick={() => handleNumberClick(7)}>7</button>
                        <button onClick={() => handleNumberClick(8)}>8</button>
                        <button onClick={() => handleNumberClick(9)}>9</button>
                        <button onClick={() => handleNumberClick(4)}>4</button>
                        <button onClick={() => handleNumberClick(5)}>5</button>
                        <button onClick={() => handleNumberClick(6)}>6</button>
                        <button onClick={() => handleNumberClick(1)}>1</button>
                        <button onClick={() => handleNumberClick(2)}>2</button>
                        <button onClick={() => handleNumberClick(3)}>3</button>
                        <button onClick={() => handleNumberClick(0)}>0</button>
                    </div>

                    <div className={css["input-block__buttons"]}>
                        <Button
                            className={css["input-block__delete-button"]}
                            onClick={handleDelete}
                            color="red"
                        >
                            Удалить
                        </Button>
                        <Button
                            color="blue"
                            className={css["input-block__submit-button"]}
                            onClick={handleSubmit}
                            disabled={!inputValue || inputValue === "0"}
                        >
                            Отправить
                        </Button>
                    </div>
                </div>
            </div>
            <Button
                color="golden"
                disabled={!showFeedback}
                className={css["number-input-game__submit-button"]}
                onClick={() =>
                    checkGame(dayID ? dayID : 0, gameID ? gameID : 0)
                }
            >
                Завершить
            </Button>
        </div>
    );
}
