import { useEffect, useRef, useState } from "react";
import { Text } from "../../components/Text/Text";
import { Game } from "../../store/main.reducer";
import css from "./CatchItemGame.module.css";
import classNames from "classnames";
import CheckmarkIcon from "../../icons/CheckmarkIcon/CheckmarkIcon";

export interface CatchItemGameProps {
    game?: Game;
    dayID?: number;
    gameID?: number;
    checkGame: (dayID: number, gameID: number) => {};
}

interface GameItem {
    id: number;
    pic: string;
}

interface CatchGame {
    title: string;
    items: GameItem[];
    obstacles: GameItem[];
}

interface FallingObject {
    id: number;
    type: "item" | "obstacle";
    pic: string;
    itemId: number;
    x: number;
    duration: number;
}

interface CatchGames {
    [key: number]: CatchGame;
}

export default function CatchItemGame({
    game,
    dayID,
    gameID,
    checkGame,
}: CatchItemGameProps) {
    const catchGames: CatchGames = {
        11: {
            title: "Поймайте 5 «правильных ингредиентов»",
            items: [
                {
                    id: 1,
                    pic: "assets/images/days/day3/game1/chocolate-icon.png",
                },
                { id: 2, pic: "assets/images/days/day3/game1/nut-icon.png" },
                { id: 3, pic: "assets/images/days/day3/game1/waffle-icon.png" },
                { id: 4, pic: "assets/images/days/day3/game1/milk-icon.png" },
                {
                    id: 5,
                    pic: "assets/images/days/day3/game1/caramel-icon.png",
                },
            ],
            obstacles: [
                { id: 1, pic: "assets/images/days/day3/game1/obstacle1.png" },
                { id: 2, pic: "assets/images/days/day3/game1/obstacle2.png" },
                { id: 3, pic: "assets/images/days/day3/game1/obstacle3.png" },
            ],
        },
    };

    const currentGame = catchGames[gameID || 11];
    const [lives, setLives] = useState(3);
    const [score, setScore] = useState(0);
    const [caughtItems, setCaughtItems] = useState<number[]>([]);
    const [fallingObjects, setFallingObjects] = useState<FallingObject[]>([]);
    const [gameOver, setGameOver] = useState(false);
    const [gameWon, setGameWon] = useState(false);
    const fieldRef = useRef<HTMLDivElement>(null);
    const animationTimers = useRef<Map<number, NodeJS.Timeout>>(new Map());

    // Генерация падающих объектов
    useEffect(() => {
        if (!currentGame || gameOver || gameWon) return;

        const generateObject = () => {
            const isItem = Math.random() > 0.4;
            const objects = isItem ? currentGame.items : currentGame.obstacles;
            const randomObject =
                objects[Math.floor(Math.random() * objects.length)];

            const newObject: FallingObject = {
                id: Date.now() + Math.random(),
                type: isItem ? "item" : "obstacle",
                pic: randomObject.pic,
                itemId: randomObject.id,
                x: Math.random() * 85,
                duration: 4 + Math.random() * 2, // 4-6 секунд для падения
            };

            setFallingObjects((prev) => [...prev, newObject]);

            // Автоматическое удаление объекта после завершения анимации
            const timer = setTimeout(() => {
                setFallingObjects((prev) =>
                    prev.filter((obj) => obj.id !== newObject.id)
                );
                animationTimers.current.delete(newObject.id);
            }, newObject.duration * 1000);

            animationTimers.current.set(newObject.id, timer);
        };

        // Генерируем объекты чаще, но с меньшим количеством одновременно
        const interval = setInterval(generateObject, 500); // Новый объект каждые 0.5 секунд

        return () => {
            clearInterval(interval);
            // Очищаем все таймеры при размонтировании
            animationTimers.current.forEach((timer) => clearTimeout(timer));
            animationTimers.current.clear();
        };
    }, [currentGame, gameOver, gameWon]);

    // Проверка на победу
    useEffect(() => {
        if (score === currentGame?.items.length) {
            setGameWon(true);
            checkGame(dayID ? dayID : 0, gameID ? gameID : 0);
        }
    }, [score, currentGame]);

    // Проверка на проигрыш
    useEffect(() => {
        if (lives <= 0) {
            setGameOver(true);
        }
    }, [lives]);

    const handleObjectClick = (object: FallingObject) => {
        // Очищаем таймер анимации для этого объекта
        const timer = animationTimers.current.get(object.id);
        if (timer) {
            clearTimeout(timer);
            animationTimers.current.delete(object.id);
        }

        if (object.type === "item") {
            if (!caughtItems.includes(object.itemId)) {
                setScore((prev) => prev + 1);
                setCaughtItems((prev) => [...prev, object.itemId]);
            }
        } else {
            setLives((prev) => prev - 1);
        }

        // Удаляем объект при клике
        setFallingObjects((prev) => prev.filter((obj) => obj.id !== object.id));
    };

    const restartGame = () => {
        // Очищаем все таймеры
        animationTimers.current.forEach((timer) => clearTimeout(timer));
        animationTimers.current.clear();

        setLives(3);
        setScore(0);
        setCaughtItems([]);
        setFallingObjects([]);
        setGameOver(false);
        setGameWon(false);
    };

    if (!currentGame) {
        return <div>Игра не найдена</div>;
    }

    return (
        <div className={css["catch-item-game"]}>
            <div className={css["catch-item-header"]}>
                <div className={css["catch-item-counter"]}>
                    <div className={css["catch-item-counter__content"]}>
                        {score}/{currentGame.items.length}
                    </div>
                </div>

                <div className={css["catch-item-targets"]}>
                    <div className={css["catch-item-targets__content"]}>
                        <Text
                            size="small"
                            align="left"
                            className={css["catch-item-targets__title"]}
                        >
                            {currentGame.title}
                        </Text>
                        <div className={css["catch-item-items"]}>
                            {currentGame.items.map((item) => (
                                <div
                                    className={classNames(
                                        css["catch-item-items__item"],
                                        caughtItems.includes(item.id) &&
                                            css["catch-item-items__item_active"]
                                    )}
                                    key={item.id}
                                >
                                    {caughtItems.includes(item.id) ? <CheckmarkIcon width={20} fill="#ffffff" /> : <img width={25} src={item.pic} alt="" />}
                                    
                                </div>
                            ))}
                        </div>
                    </div>
                </div>

                <div className={css["catch-item-lives"]}>
                    <div className={css["catch-item-lives__content"]}>
                        {Array.from({ length: 3 }, (_, index) => (
                            <img
                                key={index}
                                src="assets/images/heart-icon.svg"
                                alt=""
                                style={{ opacity: index < lives ? 1 : 0.3 }}
                            />
                        ))}
                    </div>
                </div>
            </div>

            <div className={css["catch-item-field"]} ref={fieldRef}>
                {fallingObjects.map((object) => (
                    <div
                        key={object.id}
                        className={css["falling-object"]}
                        style={{
                            left: `${object.x}%`,
                            animationDuration: `${object.duration}s`,
                        }}
                        onClick={() => handleObjectClick(object)}
                    >
                        <img
                            src={object.pic}
                            alt=""
                            className={css["falling-object__image"]}
                        />
                    </div>
                ))}

                {/* {(gameOver || gameWon) && (
                    <div className={css["game-overlay"]}>
                        <div className={css["game-message"]}>
                            <h2>{gameWon ? "Победа!" : "Игра окончена"}</h2>
                            <p>
                                {gameWon
                                    ? "Вы поймали все ингредиенты!"
                                    : "Закончились жизни"}
                            </p>
                            <button onClick={restartGame} className={css["restart-button"]}>
                                Играть снова
                            </button>
                        </div>
                    </div>
                )} */}
            </div>
        </div>
    );
}
