import { ApiDay, Day, DayType, Game, GameType } from "../store/main.reducer";

// Маппинг названий дней к типам
const dayTypeMap: { [key: string]: DayType } = {
    Шоколад: DayType.CHOCOLATE,
    Орехи: DayType.NUT,
    Вафля: DayType.WAFFLE,
    Молоко: DayType.MILK,
    Карамель: DayType.CARAMEL,
};

const prizeImagesByOrder: string[] = [
    "assets/images/prizes/prize-litres.png", // День 1
    "assets/images/prizes/prize-arzamas.png", // День 2
    "assets/images/prizes/prize-ozon.png", // День 3
    "assets/images/prizes/prize-skillbox.png", // День 4
    "assets/images/prizes/prize-sertif.png", // День 5
];

export const transformProgressData = (
    apiData: ApiDay[],
    baseDaysData: Day[] // Принимаем базовые данные дней из стейта
): Day[] => {
    return apiData.map((apiDay) => {
        const dayNumber = parseInt(apiDay.day_number);

        // Определяем доступность дня
        const isActive =
            apiDay.day_status === "Open" ||
            apiDay.day_status === "Open Penalty Complete" ||
            apiDay.day_status === "End Success";
        const isPenalty = apiDay.day_status === "Open Penalty";
        const isDone = apiDay.day_complete_time !== "";

        // Находим соответствующий день в базовых данных
        const baseDay = baseDaysData.find((day) => day.id === dayNumber);

        // Преобразуем игры, объединяя данные из API и базовые данные
        const games: Game[] = baseDay
            ? baseDay.games.map((baseGame, gameIndex) => {
                  // Находим соответствующую игру в API данных
                  const apiGame = apiDay.day_games[gameIndex];

                  return {
                      ...baseGame, // Берем все данные из базовой конфигурации
                      available: isActive && baseGame.available, // Сохраняем availability из базовых данных, но учитываем активность дня
                      done: apiGame ? apiGame.game_completed : baseGame.done, // Берем статус выполнения из API
                      // Другие поля, которые могут обновляться из API
                  } as Game;
              })
            : []; // Если базовых данных нет, возвращаем пустой массив

        return {
            id: dayNumber,
            pic:
                prizeImagesByOrder[dayNumber - 1] ||
                "assets/images/prizes/prize-litres.png",
            name: apiDay.day_title,
            active: isActive,
            done: isDone,
            penalty: isPenalty,
            type: dayTypeMap[apiDay.day_title] || DayType.CHOCOLATE,
            icon:
                `assets/images/${dayTypeMap[
                    apiDay.day_title
                ]?.toLowerCase()}-icon.png` ||
                "assets/images/chocolate-icon.png",
            time: `${apiDay.day_open_date}T00:00:00+03:00`,
            games: games,
        } as Day;
    });
};
